<script>
import { inject, onMounted, toRefs, watch } from "vue";
import { useOvariannFormInit } from "@/composable/born";
import { emitter } from "@/event-bus";
export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            loading,
            form,
            v$,
            save,
            fillForm,
            typeOfStimulationProtocolOptions,
            triggerMedicationOptions,
            medicationForCOSOptions,
            reasonCancelledOptions,
            adjuvantMedicationOptions
        } = useOvariannFormInit();

        watch(() => store.state.born.ovarian, fillForm);

        const { isInBornPage } = toRefs(props);
        const bornForm = inject("bornForm", null);
        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.ovarian = { ...newValue };
        });

        watch(
            () => form.medicationForCOS,
            (newVal) => {
                emitter.emit(
                    "ovarian-form:medication-cos-contains-fsh-or-lh-or-fsh+lh",
                    newVal.includes(4) ||
                        newVal.includes(5) ||
                        newVal.includes(6)
                );
                emitter.emit(
                    "ovarian-form:medication-cos-changed",
                    form.medicationForCOS
                );
            }
        );

        onMounted(() => {
            if (isInBornPage.value) {
                return;
            }

            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        return {
            isInBornPage,
            form,
            v$,
            save,
            loading,
            typeOfStimulationProtocolOptions,
            triggerMedicationOptions,
            medicationForCOSOptions,
            reasonCancelledOptions,
            adjuvantMedicationOptions
        };
    }
};
</script>
